.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
  margin-top: 13px;
  margin-bottom: 10px;

  @media (min-width: @tablet-width) {
    margin-top: 25px;
  }

  @media (min-width: @desktop-width) {
    margin-top: 60px;
    margin-bottom: 71px;
  }
}

@media (min-width: @tablet-width) {
  .header--index {
    margin-top: -5px;
  }
}

.header__div {
  display: flex;
  gap: 56px;
  align-items: center;

  @media (min-width: @tablet-width) {
    gap: 14px;
  }
}

.header__logo-img-desktop {
  display: none;

  @media (min-width: @tablet-width) {
    display: none;
  }

  @media (min-width: @desktop-width) {
    display: block;
  }
}

.header__logo-img-tablet {
  display: none;

  @media (min-width: @tablet-width) {
    display: block;
  }

  @media (min-width: @desktop-width) {
    display: none;
  }
}

.header__logo-img-mobil {
  display: block;

  @media (min-width: @tablet-width) {
    display: none;
  }

  @media (min-width: @desktop-width) {
    display: none;
  }
}
